* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  &::before,
  &::after {
    box-sizing: border-box;
  }
  &:focus {
    outline: 0;
    box-shadow: none;
    border: none;
  }
}

html, body {
  background-color: beige;
  font: 14px/18px Arial, sans-serif;
  height: 100%;
}
@media (any-hover: hover) {
  //scrollbar
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;

    &-track {
      background: #fff;
    }
    &-thumb {
      background: #ccc;
      border-radius: 4px;

      &:hover {
        background: #999;
      }
    }
  }
  @-moz-document url-prefix() {
    body {
      scrollbar-color: #E3D3BF transparent;
      scrollbar-width: thin;
    }
  }
}

#root {
  height: 100%;
}

[disabled] {
  pointer-events: none;
  opacity: .25;
}

a {
  text-decoration: none;
  color: #3389f1;
  font-weight: normal;
  transition: .15s;

  &:hover {
    color: #606060;
  }
  &:active {
    color: #000;
  }
}
button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: .2s;
}
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 32px;
  font-size: 16px;

  &--primary {
    background-color: #3389f1;
    color: #fff;
    padding: 0 24px;
    border-radius: 32px;

    &:hover {
      background-color: transparentize(#3389f1, .15);
    }
  }
}
input {
  //Hide arrow input number
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  &[type=number] {
    -moz-appearance: textfield;

    &:focus {
      border-color: #e0e0e0;
    }
  }
}

.input {
  border: 1px solid #e0e0e0;
  background-color: transparent;
  min-height: 32px;
  padding: 0 8px;
  font-size: 14px;
  line-height: 100%;
  border-radius: 4px;
  transition: .2s;

  &:focus {
    outline: 0;
    border: 1px solid #909090;

    &::placeholder {
      opacity: 0;
    }
  }

  &:focus~.text--placeholder,
  &:not(:placeholder-shown)~.text--placeholder {
    top: -8px;
    font-size: 10px;
    line-height: 12px;
    font-weight: 400;
    background-color: #fff;
    z-index: 1;
    padding: 2px 8px;
  }
}
textarea {
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  resize: none;
  padding: 8px;
  width: 100%;
  min-height: 100px;
  transition: .2s;

  &:focus {
    outline: 0;
    border: 1px solid #909090;
  }
}
img {
  border: none;
  max-width: 100%;
}
//transition
a {
  transition: .2s;
}

//nav
nav {
  ul {
    list-style: none;
  }
}

//typographic
h1, h2, h3, h4, h5, h6 {
  padding: 16px 0;
}
.text-ellipse-3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
}
.text {
  line-height: 21px;

  &--small {
    font-size: 10px;
    line-height: 12px;
    color: #606060;
  }
  &--placeholder {
    font-size: 13px;
    font-weight: 400;
    line-height: 19px;
    color: #909090;
    position: absolute;
    left: 15px;
    top: 6px;
    cursor: text;
    -webkit-transition: top .2s ease-in-out;
    transition: top .2s ease-in-out
  }
}

//flex options
.mesh--cell {
  display: flex;
  flex-direction: column;
}
.mesh--row {
  display: flex;
}
.mesh--wrap {
  flex-wrap: wrap;
}
.mesh--row-v-center {
  align-items: center;
}
[data-flex="0"] {
  flex: 0 0 auto;
}
[data-flex="1"] {
  flex: 1 1 auto;
}

//load animation
@keyframes load {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}